        "ÂçïÊù°ÊÄªÁªì/‰∏ªÈ¢òÊÄªÁªìÊîæÊúÄÊñ∞‰∏ÄÊù°",
        "Êó•Êúü",
        "ÂõΩÂÆ∂",
        "ËêΩÂú∞Â™í‰Ωì",
        "ËêΩÂú∞Âπ≥Âè∞",
        "ËêΩÂú∞Ê¨°Êï∞",
        "ÈìæÊé•",
        "Êà™Âõæ",
        "ÂÖ∂‰ªñ",
        "ÂÖ∂‰ªñ_1",
      ],
      filteredData: [], // Á≠õÈÄâÂêéÁöÑÊï∞ÊçÆ
      allData: [], // ÊâÄÊúâÊï∞ÊçÆÔºàÁî®‰∫éÊêúÁ¥¢Á≠õÈÄâÔºâ
      originalData: [], // ÂéüÂßãÊï∞ÊçÆÂ§á‰ªΩ
      tableHeight: 600, // Ë°®Ê†ºÈ´òÂ∫¶
      isFullscreen: false, // ÊòØÂê¶ÂÖ®Â±èÊòæÁ§∫
      dateRange: [], // Êó•ÊúüËåÉÂõ¥ÈÄâÊã©
      isDateRangeInvalid: false, // Êó•ÊúüËåÉÂõ¥ÊòØÂê¶Êó†Êïà,
      tagExpression: "", // Ê†áÁ≠æÊêúÁ¥¢Ë°®ËææÂºè
      expressionError: "", // Ë°®ËææÂºèÈîôËØØ‰ø°ÊÅØ
      searchType: "", // ÊêúÁ¥¢Á±ªÂûãÔºàÂõΩÂÆ∂/ËêΩÂú∞Â™í‰ΩìÔºâ
      searchKeyword: "", // ÊêúÁ¥¢ÂÖ≥ÈîÆËØç
