      statisticsData: [], // ÁªüËÆ°Êï∞ÊçÆ
      isFromStatistics: false, // Ê†áËØÜÊòØÂê¶‰ªéÁªüËÆ°Èù¢ÊùøÊâìÂºÄËØ¶ÊÉÖ
      detailDialogVisible: false, // ËØ¶ÊÉÖÂØπËØùÊ°ÜÊòæÁ§∫Áä∂ÊÄÅ
      detailContent: "", // ËØ¶ÊÉÖÂÜÖÂÆπ
