      detailRow: 0, // ËØ¶ÊÉÖË°åÂè∑
      detailSequenceValue: "", // ËØ¶ÊÉÖÂ∫èÂè∑ÂÄº
      sequenceGroups: null, // Áî®‰∫éÂ≠òÂÇ®Â∫èÂè∑ÂàÜÁªÑ‰ø°ÊÅØ
      currentGroupId: 0, // ÂΩìÂâçÂàÜÁªÑID
      currentSequenceValue: null, // ÂΩìÂâçÂàÜÁªÑÁöÑÂ∫èÂè∑ÂÄº
      hasSignatureArticleTag: false, // Ê†áËÆ∞ÊòØÂê¶ÂåÖÂê´"ÁΩ≤ÂêçÊñáÁ´†"Ê†áÁ≠æ
      loading: false, // Ê∑ªÂä†Âä†ËΩΩÁä∂ÊÄÅ
      mediaStatisticsDialogVisible: false, // ÂàÜÂ™í‰ΩìÁªüËÆ°ÂØπËØùÊ°ÜÊòæÁ§∫Áä∂ÊÄÅ
      mediaStatisticsData: [], // ÂàÜÂ™í‰ΩìÁªüËÆ°Êï∞ÊçÆ
    };
  },

  // ÁõëÂê¨Âô®ÔºöÁõëÂê¨Êï∞ÊçÆÂèòÂåñ
  watch: {
    data: {
      immediate: true,
      handler: function (newData) {
        // ÂÖàËøõË°åÂàóË°•ÂÖ®ÔºåÂÜçÂ°´ÂÖÖÂ∫èÂè∑ÂàóÁ©∫ÁôΩÂÄº
        // Ê∑ªÂä†Ë∞ÉËØïÊó•ÂøóÁ°ÆËÆ§ÊñπÊ≥ïÊâßË°å
        console.log("ÂºÄÂßãÊï∞ÊçÆÈ¢ÑÂ§ÑÁêÜ");
        // ‰øÆÊ≠£ÔºöÂÖàË∞ÉÁî®fillSequenceNumbersÁîüÊàê_sequenceGroupÔºåÂÜçË∞ÉÁî®fillGroupColumnsËøõË°åÂàÜÁªÑË°•ÂÖ®
        const withSequenceData = this.fillSequenceNumbers(newData);
        const processedData = this.fillGroupColumns(withSequenceData);
        const dataWithFilteredColumns = this.filterEmptyColumns(processedData);
        this.originalData = dataWithFilteredColumns;
        this.allData = dataWithFilteredColumns;
        this.filteredData = dataWithFilteredColumns;
        this.$nextTick(() => {
          this.calculateTableHeight();
        });
      },
    },
  },

  // ÁîüÂëΩÂë®ÊúüÈí©Â≠êÔºöÁªÑ‰ª∂ÊåÇËΩΩÂêé
  mounted() {
    // Ê∑ªÂä†Â∞èÂª∂ËøüÁ°Æ‰øùÁà∂ÂÆπÂô®Â∑≤ÂÆåÂÖ®Ê∏≤Êüì
    setTimeout(() => {
      this.calculateTableHeight();
    }, 100);
    window.addEventListener("resize", this.calculateTableHeight);
  },

  // ÁîüÂëΩÂë®ÊúüÈí©Â≠êÔºöÁªÑ‰ª∂ÈîÄÊØÅÂâç
  beforeDestroy() {
    window.removeEventListener("resize", this.calculateTableHeight); // ÁßªÈô§Á™óÂè£Â§ßÂ∞èÂèòÂåñÁõëÂê¨
  },

  methods: {
    /**
     * Ë°•ÂÖ®Âêå‰∏ÄÂ∫èÂè∑ÁªÑÁöÑÊåáÂÆöÂàóÊï∞ÊçÆ
     * @param {Array} data - ÂéüÂßãÊï∞ÊçÆ
     * @returns {Array} Ë°•ÂÖ®ÂêéÁöÑÊï∞ÊçÆ
     */
    fillGroupColumns(data) {
      if (!data || data.length === 0) return [];

      const processedData = JSON.parse(JSON.stringify(data));
      // ‰ΩøÁî®Â∑≤ÊúâÁöÑ_sequenceGroupÂ±ûÊÄßËøõË°åÂàÜÁªÑÔºåËÄå‰∏çÊòØÈáçÊñ∞Ëß£ÊûêÂ∫èÂè∑Âàó
      const sequenceGroups = {};

      processedData.forEach((row) => {
        const groupId = row._sequenceGroup;
        if (groupId) {
          if (!sequenceGroups[groupId]) {
            sequenceGroups[groupId] = [];
          }
          sequenceGroups[groupId].push(row);
        }
      });

      // ÈúÄË¶ÅË°•ÂÖ®ÁöÑÂàó
      const columnsToFill = ["Á®ø‰ª∂Ê†áÈ¢ò", "Ê†áÁ≠æ", "ÂçïÊù°ÊÄªÁªì/‰∏ªÈ¢òÊÄªÁªìÊîæÊúÄÊñ∞‰∏ÄÊù°"];

      // ÂØπÊØè‰∏™Â∫èÂè∑ÁªÑËøõË°åÂàóË°•ÂÖ®
      Object.values(sequenceGroups).forEach((group) => {
        if (group.length > 0) {
          // Ëé∑ÂèñÁ¨¨‰∏ÄË°å‰Ωú‰∏∫ÂèÇËÄÉ
          const firstRow = group[0];

          // ÂØπÁªÑÂÜÖÊâÄÊúâË°åËøõË°åË°•ÂÖ®ÔºàÂåÖÊã¨Á¨¨‰∏ÄË°åÔºåÁ°Æ‰øùÊ≤°ÊúâÁ©∫ÂÄºÔºâ
          group.forEach((row) => {
            columnsToFill.forEach((column) => {
              // Â¶ÇÊûúÂΩìÂâçË°åËØ•ÂàóÊ≤°ÊúâÂÄºÔºå‰ΩøÁî®Á¨¨‰∏ÄË°åÁöÑÂÄºË°•ÂÖ®
              if (
                row[column] === undefined ||
                row[column] === null ||
                row[column] === ""
              ) {
                row[column] = firstRow[column];
              }
            });
          });
        }
      });

      return processedData;
    },

    /**
     * ËøáÊª§Á©∫ÂàóÔºàÈô§Ë°®Â§¥Â§ñÂÆåÂÖ®Ê≤°ÊúâÂÜÖÂÆπÁöÑÂàóÔºâ
     * @param {Array} data - ÂéüÂßãÊï∞ÊçÆ
     * @returns {Array} ËøáÊª§ÂêéÁöÑÊï∞ÊçÆ
     */
    filterEmptyColumns(data) {
      if (!data || data.length === 0) return [];

      // Â§çÂà∂‰∏Ä‰ªΩÊï∞ÊçÆ
      const newData = JSON.parse(JSON.stringify(data));
      const columnsToRemove = [];

      // Ê£ÄÊü•ÊØè‰∏ÄÂàóÊòØÂê¶‰∏∫Á©∫
      this.headers.forEach((header) => {
        let columnHasContent = false;

        // Ê£ÄÊü•ËØ•ÂàóÊòØÂê¶ÊúâÂÜÖÂÆπ
        for (let i = 0; i < newData.length; i++) {
          const value = newData[i][header];
          if (value !== null && value !== undefined && value !== "") {
            columnHasContent = true;
            break;
          }
        }

        // Â¶ÇÊûúÂàóÊ≤°ÊúâÂÜÖÂÆπÔºåÊ†áËÆ∞‰∏∫Ë¶ÅÂà†Èô§
        if (!columnHasContent) {
          columnsToRemove.push(header);
        }
      });

      // Êõ¥Êñ∞Ë°®Â§¥ÔºåÁßªÈô§Á©∫Âàó
      this.headers = this.headers.filter(
        (header) => !columnsToRemove.includes(header)
      );

      // ‰ªéÊï∞ÊçÆ‰∏≠ÁßªÈô§Á©∫Âàó
      newData.forEach((row) => {
        columnsToRemove.forEach((column) => {
          delete row[column];
        });
      });

      return newData;
    },

    /**
     * Â§ÑÁêÜÊó•ÊúüËåÉÂõ¥ÂèòÂåñ
     */
    handleDateChange() {
      if (this.dateRange && this.dateRange.length === 2) {
        const startDate = new Date(this.dateRange[0]);
        const endDate = new Date(this.dateRange[1]);
        this.isDateRangeInvalid = endDate < startDate;

        if (this.isDateRangeInvalid) {
          this.$message.warning({
            message: "ÁªìÊùüÊó•Êúü‰∏çËÉΩÊó©‰∫éÂºÄÂßãÊó•Êúü",
            center: true,
          });
        }
      }
    },

    /**
     * Ëß£ÊûêÊó•ÊúüÂ≠óÁ¨¶‰∏≤
     * @param {string} dateStr - Êó•ÊúüÂ≠óÁ¨¶‰∏≤ÔºåÂ¶Ç"2025Âπ¥1Êúà2Êó•"Êàñ"1Êúà2Êó•"
     * @returns {Date} Ëß£ÊûêÂêéÁöÑÊó•ÊúüÂØπË±°
     */
    parseDate(dateStr) {
      if (!dateStr) return null;

      // Ê£ÄÊü•ÊòØÂê¶ÂåÖÂê´Âπ¥‰ªΩ
      const hasYear = /\d{4}Âπ¥/.test(dateStr);

      // ÊèêÂèñÂπ¥„ÄÅÊúà„ÄÅÊó•
      let year, month, day;

      if (hasYear) {
        // Â∏¶Âπ¥‰ªΩÁöÑÊ†ºÂºèÔºöYYYYÂπ¥MMÊúàDDÊó•
        const match = dateStr.match(/(\d{4})Âπ¥(\d{1,2})Êúà(\d{1,2})Êó•/);
        if (!match) return null;

        year = parseInt(match[1]);
        month = parseInt(match[2]) - 1; // Êúà‰ªΩ‰ªé0ÂºÄÂßã
        day = parseInt(match[3]);
      } else {
        // ‰∏çÂ∏¶Âπ¥‰ªΩÁöÑÊ†ºÂºèÔºöMMÊúàDDÊó•ÔºåÈªòËÆ§‰ΩøÁî®ÂΩìÂâçÂπ¥‰ªΩ
        const match = dateStr.match(/(\d{1,2})Êúà(\d{1,2})Êó•/);
        if (!match) return null;

        year = new Date().getFullYear();
        month = parseInt(match[1]) - 1; // Êúà‰ªΩ‰ªé0ÂºÄÂßã
        day = parseInt(match[2]);
      }

      return new Date(year, month, day);
    },

    /**
     * Ëß£ÊûêÊ†áÁ≠æÂ≠óÁ¨¶‰∏≤ÔºåÊîØÊåÅ‰∏≠Ëã±ÊñáÂàÜÂè∑ÂàÜÈöî
     * @param {string} tagStr - Ê†áÁ≠æÂ≠óÁ¨¶‰∏≤
     * @returns {Array} Ê†áÁ≠æÊï∞ÁªÑ
     */
    parseTags(tagStr) {
      if (!tagStr) return [];
      // ‰ΩøÁî®Ê≠£ÂàôË°®ËææÂºèÂàÜÂâ≤‰∏≠Ëã±ÊñáÂàÜÂè∑ÔºåÂπ∂ËøáÊª§Á©∫Ê†áÁ≠æ
      return tagStr
        .split(/[Ôºõ;]/)
        .map((tag) => tag.trim())
        .filter((tag) => tag);
    },

    /**
     * ËØçÊ≥ïÂàÜÊûêÂô®ÔºöÂ∞ÜË°®ËææÂºèÂ≠óÁ¨¶‰∏≤ËΩ¨Êç¢‰∏∫Ê†áËÆ∞ÊµÅ
     * @param {string} expression - ÊêúÁ¥¢Ë°®ËææÂºè
     * @returns {Array} Ê†áËÆ∞Êï∞ÁªÑ
     */
    tokenizeExpression(expression) {
      const tokens = [];
      let currentToken = "";
      let i = 0;
      const len = expression.length;

      // ÊîØÊåÅÁöÑËøêÁÆóÁ¨¶Êò†Â∞Ñ
      const operatorMap = {
        AND: "AND",
        OR: "OR",
        NOT: "NOT",
        Âíå: "AND",
        Êàñ: "OR",
        Èùû: "NOT",
        ‰∏çÂåÖÊã¨: "NOT",
      };

      // ËøêÁÆóÁ¨¶ÂàóË°®ÔºåÁî®‰∫éÂà§Êñ≠ÂÆåÊï¥ÂçïËØçÂåπÈÖç
      const operators = new Set([
        "AND",
        "OR",
        "NOT",
        "Âíå",
        "Êàñ",
        "Èùû",
        "‰∏çÂåÖÊã¨",
      ]);

      while (i < len) {
        const char = expression[i];

        // Ë∑≥ËøáÁ©∫Ê†º
        if (/\s/.test(char)) {
          if (currentToken) {
            // ÂÖàÊ£ÄÊü•ÊòØÂê¶‰∏∫ÂÆåÊï¥ËøêÁÆóÁ¨¶ÂçïËØç
            if (operators.has(currentToken)) {
              tokens.push({
                type: "OPERATOR",
                value: operatorMap[currentToken],
              });
            } else {
              tokens.push({ type: "TAG", value: currentToken });
            }
            currentToken = "";
          }
          i++;
          continue;
        }

        // Â§ÑÁêÜÊã¨Âè∑ÔºàÊîØÊåÅ‰∏≠Ëã±ÊñáÊã¨Âè∑Ôºâ
        if (char === "(" || char === ")" || char === "Ôºà" || char === "Ôºâ") {
          if (currentToken) {
            if (operators.has(currentToken)) {
              tokens.push({
                type: "OPERATOR",
                value: operatorMap[currentToken],
              });
            } else {
              tokens.push({ type: "TAG", value: currentToken });
            }
            currentToken = "";
          }
          // Áªü‰∏ÄËΩ¨Êç¢‰∏∫Ëã±ÊñáÊã¨Âè∑‰ª•‰æøÂêéÁª≠Â§ÑÁêÜ
          const normalizedParen =
            char === "Ôºà" ? "(" : char === "Ôºâ" ? ")" : char;
          tokens.push({ type: "PAREN", value: normalizedParen });
          i++;
          continue;
        }

        // Â§ÑÁêÜÂÖ∂‰ªñÂ≠óÁ¨¶
        currentToken += char;
        i++;
      }

      // Â§ÑÁêÜÊúÄÂêé‰∏Ä‰∏™token
      if (currentToken) {
        if (operators.has(currentToken)) {
          tokens.push({ type: "OPERATOR", value: operatorMap[currentToken] });
        } else {
          tokens.push({ type: "TAG", value: currentToken });
        }
      }

      // Âú®NOTËøêÁÆóÁ¨¶ÂâçËá™Âä®Ê∑ªÂä†ANDÔºàÂ¶ÇÊûúÂâçÈù¢ÊòØÊ†áÁ≠æÊàñÂè≥Êã¨Âè∑Ôºâ
      for (let i = 0; i < tokens.length; i++) {
        if (tokens[i].type === "OPERATOR" && tokens[i].value === "NOT") {
          if (
            i > 0 &&
            (tokens[i - 1].type === "TAG" || tokens[i - 1].value === ")")
          ) {
            tokens.splice(i, 0, { type: "OPERATOR", value: "AND" });
            i++; // Ë∑≥ËøáÊñ∞ÊèíÂÖ•ÁöÑAND
          }
        }
      }

      return tokens;
    },

    /**
     * ËØ≠Ê≥ïÂàÜÊûêÂô®ÔºöÂ∞ÜÊ†áËÆ∞ÊµÅËΩ¨Êç¢‰∏∫ÊäΩË±°ËØ≠Ê≥ïÊ†ë(AST)
     * @param {Array} tokens - Ê†áËÆ∞Êï∞ÁªÑ
     * @returns {Object} ÊäΩË±°ËØ≠Ê≥ïÊ†ë
     */
    parseExpression(tokens) {
      let index = 0;

      // Ëß£ÊûêË°®ËææÂºè
      function parseExpression() {
        let node = parseTerm();

        while (
          index < tokens.length &&
          tokens[index].type === "OPERATOR" &&
          tokens[index].value === "OR"
        ) {
          const operator = tokens[index++];
          const right = parseTerm();
          node = {
            type: "OPERATOR",
            value: operator.value,
            left: node,
            right: right,
          };
        }

        return node;
      }

      // Ëß£ÊûêÈ°πÔºàÂ§ÑÁêÜANDËøêÁÆóÁ¨¶Ôºâ
      function parseTerm() {
        let node = parseFactor();

        // Âè™Â§ÑÁêÜANDËøêÁÆóÁ¨¶ÔºåNOTÂú®parseFactor‰∏≠Â§ÑÁêÜ
        while (
          index < tokens.length &&
          tokens[index].type === "OPERATOR" &&
          tokens[index].value === "AND"
        ) {
          const operator = tokens[index++];
          const right = parseFactor();
          node = {
            type: "OPERATOR",
            value: operator.value,
            left: node,
            right: right,
          };
        }

        return node;
      }

      // Ëß£ÊûêÂõ†Â≠êÔºàÂ§ÑÁêÜNOTËøêÁÆóÁ¨¶ÂíåÊã¨Âè∑Ôºâ
      function parseFactor() {
        if (index >= tokens.length) {
          throw new Error("Ë°®ËææÂºè‰∏çÂÆåÊï¥");
        }

        const token = tokens[index];

        if (token.type === "OPERATOR" && token.value === "NOT") {
          index++;
          const operand = parseFactor();
          return { type: "OPERATOR", value: "NOT", right: operand };
        } else if (token.type === "PAREN" && token.value === "(") {
          index++; // Ë∑≥ËøáÂ∑¶Êã¨Âè∑
          const expr = parseExpression();
          if (
            index >= tokens.length ||
            tokens[index].type !== "PAREN" ||
            tokens[index].value !== ")"
          ) {
            throw new Error("Áº∫Â∞ëÂè≥Êã¨Âè∑");
          }
          index++; // Ë∑≥ËøáÂè≥Êã¨Âè∑
          return expr;
        } else if (token.type === "TAG") {
          index++;
          return { type: "TAG", value: token.value };
        } else {
          throw new Error(`ÊÑèÂ§ñÁöÑÊ†áËÆ∞: ${token.value}`);
        }
      }

      const ast = parseExpression();

      // Ê£ÄÊü•ÊòØÂê¶ÊúâÂâ©‰ΩôÊ†áËÆ∞
      if (index < tokens.length) {
        throw new Error(`ÊÑèÂ§ñÁöÑÊ†áËÆ∞: ${tokens[index].value}`);
      }

      return ast;
    },
    /**
     * Â∞ÜASTËΩ¨Êç¢‰∏∫ÈÄªËæëË°®ËææÂºèÂ≠óÁ¨¶‰∏≤
     * @param {Object} ast - ÊäΩË±°ËØ≠Ê≥ïÊ†ë
     * @returns {string} ÈÄªËæëË°®ËææÂºèÂ≠óÁ¨¶‰∏≤
     */
    astToLogicalString(ast) {
      if (!ast) return "";

      switch (ast.type) {
        case "TAG":
          return ast.value;

        case "OPERATOR":
          switch (ast.value) {
            case "AND":
              return `(${this.astToLogicalString(
                ast.left
              )} && ${this.astToLogicalString(ast.right)})`;
            case "OR":
              return `(${this.astToLogicalString(
                ast.left
              )} || ${this.astToLogicalString(ast.right)})`;
            case "NOT":
              return `!(${this.astToLogicalString(ast.right)})`;
            default:
              return "";
          }

        default:
          return "";
      }
    },

    /**
     * ËØÑ‰º∞Ë°®ËææÂºèAST
     * @param {Object} ast - ÊäΩË±°ËØ≠Ê≥ïÊ†ë
     * @param {Set} rowTags - Ë°åÊ†áÁ≠æÈõÜÂêà
     * @returns {boolean} ËØÑ‰º∞ÁªìÊûú
     */
    evaluateExpression(ast, rowTags) {
      if (!ast) return true; // Á©∫Ë°®ËææÂºèÂåπÈÖçÊâÄÊúâ

      switch (ast.type) {
        case "TAG":
          // Ê†áÁ≠æÂåπÈÖçÂå∫ÂàÜÂ§ßÂ∞èÂÜôÔºåËΩ¨‰∏∫Â∞èÂÜôÂêéÊØîËæÉ
          const tagValue = ast.value.toLowerCase();
          return Array.from(rowTags).some(
            (tag) => tag.toLowerCase() === tagValue
          );

        case "OPERATOR":
          switch (ast.value) {
            case "AND":
              return (
                this.evaluateExpression(ast.left, rowTags) &&
                this.evaluateExpression(ast.right, rowTags)
              );
            case "OR":
              return (
                this.evaluateExpression(ast.left, rowTags) ||
                this.evaluateExpression(ast.right, rowTags)
              );
            case "NOT":
              return !this.evaluateExpression(ast.right, rowTags);
            default:
              throw new Error(`Êú™Áü•ÁöÑËøêÁÆóÁ¨¶: ${ast.value}`);
          }

        default:
          throw new Error(`Êú™Áü•ÁöÑËäÇÁÇπÁ±ªÂûã: ${ast.type}`);
      }
    },

    /**
     * Ê†πÊçÆÊ†áÁ≠æË°®ËææÂºèÁ≠õÈÄâÊï∞ÊçÆ
     * @param {Array} data - ÂéüÂßãÊï∞ÊçÆ
     * @param {string} expression - Ê†áÁ≠æÊêúÁ¥¢Ë°®ËææÂºè
     * @returns {Array} Á≠õÈÄâÂêéÁöÑÊï∞ÊçÆ
     */
    filterByTags(data, expression) {
      const trimmedExpr = expression.trim();
      if (!trimmedExpr) return data;

      try {
        this.expressionError = "";
        const tokens = this.tokenizeExpression(trimmedExpr);
        const ast = this.parseExpression(tokens);

        // Ê£ÄÊü•ÊòØÂê¶ÂåÖÂê´"ÁΩ≤ÂêçÊñáÁ´†"Ê†áÁ≠æ
        this.hasSignatureArticleTag = trimmedExpr
          .toLowerCase()
          .includes("ÁΩ≤ÂêçÊñáÁ´†");

        // ÁîüÊàêÂπ∂ËæìÂá∫ÈÄªËæëË°®ËææÂºèÂà∞ÊéßÂà∂Âè∞
        const logicalString = this.astToLogicalString(ast);
        console.log("Ê†áÁ≠æÊêúÁ¥¢ÈÄªËæë:", logicalString);

        // ÊåâÂ∫èÂè∑ÁªÑÂàÜÁªÑ
        const sequenceGroups = new Map();
        data.forEach((row) => {
          const groupId = row._sequenceGroup;
          if (!groupId) return;

          if (!sequenceGroups.has(groupId)) {
            sequenceGroups.set(groupId, []);
          }
          sequenceGroups.get(groupId).push(row);
        });

        // 2. Á≠õÈÄâÁ¨¶ÂêàÊù°‰ª∂ÁöÑÂ∫èÂè∑ÁªÑ
        const matchedGroups = [];
        sequenceGroups.forEach((groupRows) => {
          // ‰ΩøÁî®ÁªÑÂÜÖÁ¨¨‰∏ÄË°åÁöÑÊ†áÁ≠æ‰Ωú‰∏∫Êï¥‰∏™ÁªÑÁöÑÊ†áÁ≠æ
          const firstRow = groupRows[0];
          if (!firstRow["Ê†áÁ≠æ"]) return;

          const groupTags = new Set(this.parseTags(firstRow["Ê†áÁ≠æ"]));
          if (this.evaluateExpression(ast, groupTags)) {
            matchedGroups.push(...groupRows);
          }
        });

        return matchedGroups;
      } catch (error) {
        console.error("Ê†áÁ≠æË°®ËææÂºèËß£ÊûêÈîôËØØ:", error);
        this.expressionError = `Ë°®ËææÂºèÈîôËØØ: ${error.message}`;

        // Â∞ùËØïÁÆÄÂçïÊ†áÁ≠æÂåπÈÖç‰Ωú‰∏∫ÂõûÈÄÄÊñπÊ°à
        try {
          const tag = trimmedExpr;
          console.log("‰ΩøÁî®ÁÆÄÂçïÂåπÈÖçÊ®°ÂºèÊêúÁ¥¢:", tag);

          // ÂõûÈÄÄÊñπÊ°à‰πü‰ΩøÁî®ÊåâÁªÑÂåπÈÖç
          const sequenceGroups = new Map();
          data.forEach((row) => {
            const groupId = row._sequenceGroup;
            if (!groupId) return;

            if (!sequenceGroups.has(groupId)) {
              sequenceGroups.set(groupId, []);
            }
            sequenceGroups.get(groupId).push(row);
          });

          const matchedGroups = [];
          sequenceGroups.forEach((groupRows) => {
            const firstRow = groupRows[0];
            if (!firstRow["Ê†áÁ≠æ"]) return;

            const rowTags = this.parseTags(firstRow["Ê†áÁ≠æ"]);
            if (
              rowTags.some((rTag) =>
                rTag.toLowerCase().includes(tag.toLowerCase())
              )
            ) {
              matchedGroups.push(...groupRows);
            }
          });

          return matchedGroups;
        } catch (fallbackError) {
          this.expressionError = `Ë°®ËææÂºèÈîôËØØ: ${error.message}`;
          console.error("Ê†áÁ≠æÁ≠õÈÄâÈîôËØØ:", error);
          return [];
        }
      }
    },

    /**
     * Ê£ÄÊü•AST‰∏≠ÊòØÂê¶ÂåÖÂê´"ÁΩ≤ÂêçÊñáÁ´†"Ê†áÁ≠æ
     * @param {Object} ast - ÊäΩË±°ËØ≠Ê≥ïÊ†ë
     * @returns {boolean} ÊòØÂê¶ÂåÖÂê´"ÁΩ≤ÂêçÊñáÁ´†"Ê†áÁ≠æ
     */
    checkSignatureArticleTag(ast) {
      if (!ast) return false;

      if (ast.type === "TAG" && ast.value === "ÁΩ≤ÂêçÊñáÁ´†") {
        return true;
      }

      if (ast.type === "OPERATOR") {
        if (ast.left && this.checkSignatureArticleTag(ast.left)) {
          return true;
        }
        if (ast.right && this.checkSignatureArticleTag(ast.right)) {
          return true;
        }
      }

      return false;
    },

    /**
     * Ê†πÊçÆÊó•ÊúüËåÉÂõ¥ÂíåÊ†áÁ≠æÊêúÁ¥¢Êï∞ÊçÆ
     */
    searchByDateRange() {
      // ÂÖàÂ∫îÁî®Êó•ÊúüÁ≠õÈÄâ
      let filtered = JSON.parse(JSON.stringify(this.originalData));
      if (this.dateRange && this.dateRange.length === 2) {
        const startDate = new Date(this.dateRange[0]);
        const endDate = new Date(this.dateRange[1]);

        // ÊòæÂºèËÆæÁΩÆÊó∂Èó¥ÈÉ®ÂàÜÔºåÁ°Æ‰øùÂåÖÂê´Êï¥‰∏™Êó•Êúü
        startDate.setHours(0, 0, 0, 0); // Ëµ∑ÂßãÊó•Êúü‰ªéÂΩìÂ§©0ÁÇπÂºÄÂßã
        endDate.setHours(23, 59, 59, 999); // ÁªìÊùüÊó•ÊúüÂà∞ÂΩìÂ§©23:59:59ÁªìÊùü

        if (endDate < startDate) {
          this.$message.warning({
            message: "ÁªìÊùüÊó•Êúü‰∏çËÉΩÊó©‰∫éÂºÄÂßãÊó•Êúü",
            center: true,
          });
          return;
        }

        // Á≠õÈÄâÊó•ÊúüÂú®ËåÉÂõ¥ÂÜÖÁöÑÊï∞ÊçÆ
        filtered = filtered.filter((row) => {
          if (!row["Êó•Êúü"]) return false;

          const rowDate = this.parseDate(row["Êó•Êúü"]);
          if (!rowDate) return false;

          return rowDate >= startDate && rowDate <= endDate;
        });
      }
      // Â∫îÁî®ÂõΩÂÆ∂/ËêΩÂú∞Â™í‰ΩìÊêúÁ¥¢Á≠õÈÄâ
      if (this.searchType && this.searchKeyword) {
        const keyword = this.searchKeyword.toLowerCase();
        filtered = filtered.filter((row) => {
          if (!row[this.searchType]) return false;
          return String(row[this.searchType]).toLowerCase().includes(keyword);
        });
      }
      // ÂÜçÂ∫îÁî®Ê†áÁ≠æÁ≠õÈÄâ
      filtered = this.filterByTags(filtered, this.tagExpression);

      // ÂÖ≥ÈîÆ‰øÆÊîπÔºö‰ΩøÁî®Ê∑±Êã∑Ë¥ùÈÅøÂÖç‰øÆÊîπÂéüÂßãÊï∞ÊçÆ
      this.filteredData = JSON.parse(JSON.stringify(filtered));

      // Ëß¶ÂèëÁ≠õÈÄâÊï∞ÊçÆÂèòÂåñ‰∫ã‰ª∂
      this.$emit("filter-change", this.filteredData);

      // ‰∏∫ÊØè‰∏™ÂàÜÁªÑÁöÑÁ¨¨‰∏ÄË°åÊ∑ªÂä†Â∫èÂè∑
      this.addSequenceToGroupFirstRows();

      // ÈáçÁΩÆÂàÜÁªÑ‰ø°ÊÅØÔºåÁ°Æ‰øùÁ≠õÈÄâÂêéÈáçÊñ∞ËÆ°ÁÆóÂàÜÁªÑÈ¢úËâ≤
      this.sequenceGroups = null;
      this.currentGroupId = 0;

      // Â¶ÇÊûúÊ≤°ÊúâÁ≠õÈÄâÁªìÊûúÔºåÊòæÁ§∫ÊèêÁ§∫‰ø°ÊÅØ
      if (this.filteredData.length === 0) {
        this.$message.info({
          message: "Ê≤°ÊúâÊâæÂà∞Á¨¶ÂêàÊù°‰ª∂ÁöÑÊï∞ÊçÆ",
          center: true,
        });
      } else {
        // Ê£ÄÁ¥¢ÊàêÂäüÊèêÁ§∫
        this.$message.success({
          message: "Ê£ÄÁ¥¢ÊàêÂäü",
          center: true,
        });
      }
    },

    /**
     * ‰∏∫ÊØè‰∏™ÂàÜÁªÑÁöÑÁ¨¨‰∏ÄË°åÊ∑ªÂä†Â∫èÂè∑
     */
    addSequenceToGroupFirstRows() {
      if (!this.filteredData || this.filteredData.length === 0) return;

      const firstHeader = this.headers[0] || "Â∫èÂè∑";
      let lastGroup = null;

      this.filteredData.forEach((row) => {
        const currentGroup = row._sequenceGroup;

        // Â¶ÇÊûúÊòØÊñ∞ÂàÜÁªÑÁöÑÁ¨¨‰∏ÄË°å‰∏îÂ∫èÂè∑‰∏∫Á©∫ÔºåÂàôÊ∑ªÂä†ÂàÜÁªÑÂ∫èÂè∑
        if (
          currentGroup &&
          currentGroup !== lastGroup &&
          (!row[firstHeader] || row[firstHeader] === "")
        ) {
          row[firstHeader] = currentGroup;
          row[`${firstHeader}_isFiltered`] = true; // Ê†áËÆ∞‰∏∫Á≠õÈÄâÊó∂Ê∑ªÂä†ÁöÑÂ∫èÂè∑
        }

        lastGroup = currentGroup;
      });
    },

    /**
     * ÈáçÁΩÆË°®Ê†ºÂà∞ÂàùÂßãÁä∂ÊÄÅ
     */
    resetTable() {
      // ‰øùÂ≠òÂΩìÂâçÁä∂ÊÄÅÁî®‰∫éÊØîËæÉ
      const currentState = {
        dateRange: [...this.dateRange],
        tagExpression: this.tagExpression,
        searchType: this.searchType,
        searchKeyword: this.searchKeyword,
        filteredDataLength: this.filteredData.length,
      };

      // Âº∫Âà∂ÈáçÁΩÆÊâÄÊúâÁ≠õÈÄâÊù°‰ª∂
      this.dateRange = [];
      this.isDateRangeInvalid = false;
      this.tagExpression = "";
      this.expressionError = "";
      this.searchType = "";
      this.searchKeyword = "";
      // ÈáçÁΩÆÁΩ≤ÂêçÊñáÁ´†Ê†áÁ≠æÁä∂ÊÄÅ
      this.hasSignatureArticleTag = false;
      // Âº∫Âà∂ÊÅ¢Â§çÂéüÂßãÊï∞ÊçÆÔºà‰ΩøÁî®Ê∑±Êã∑Ë¥ùÁ°Æ‰øù‰∏ç‰øÆÊîπÂéüÂßãÊï∞ÊçÆÔºâ
      this.filteredData = JSON.parse(JSON.stringify(this.originalData));
      this.$emit("filter-change", this.filteredData);

      // ÂÆåÂÖ®Ê∏ÖÈô§ÊâÄÊúâÊ∑ªÂä†ÁöÑÂ∫èÂè∑ÔºàÂ¢ûÂº∫ÁâàÔºâ
      const firstHeader = this.headers[0] || "Â∫èÂè∑";
      this.filteredData.forEach((row) => {
        // Ê∏ÖÈô§ÊâÄÊúâÂèØËÉΩÁöÑÂ∫èÂè∑Ê†áËÆ∞
        if (row[`${firstHeader}_isFiltered`]) {
          row[firstHeader] = "";
          delete row[`${firstHeader}_isFiltered`];
        }
        // Á°Æ‰øùÂéüÂßãÂ∫èÂè∑Âè™‰øùÁïôÂéüÂßãÊ†áËÆ∞ÁöÑ
        if (!row[`${firstHeader}_isOriginal`]) {
          row[firstHeader] = "";
        }
      });

      // ÈáçÁΩÆÂàÜÁªÑ‰ø°ÊÅØÔºåÁ°Æ‰øùÈáçÁΩÆÂêéÈáçÊñ∞ËÆ°ÁÆóÂàÜÁªÑÈ¢úËâ≤
      this.sequenceGroups = null;
      this.currentGroupId = 0;

      // ‰ΩøÁî®$nextTickÁ°Æ‰øùDOMÊõ¥Êñ∞ÂêéÂÜçÊòæÁ§∫Ê∂àÊÅØÔºåÁ°Æ‰øùÊ∂àÊÅØÊÄªÊòØÊòæÁ§∫
      this.$nextTick(() => {
        this.$message.success({
          message: "Â∑≤Â§çÂéüË°®Ê†º",
          center: true,
          zIndex: 99999, // Á°Æ‰øùÊ∂àÊÅØÂú®ÊúÄ‰∏äÂ±ÇÊòæÁ§∫
        });
      });
    },

    /**
     * Â°´ÂÖÖÂ∫èÂè∑ÂàóÁöÑÁ©∫ÁôΩÂÄº
     * @param {Array} data - ÂéüÂßãÊï∞ÊçÆ
     * @returns {Array} Â§ÑÁêÜÂêéÁöÑÊñ∞Êï∞ÊçÆ
     */
    fillSequenceNumbers: function (data) {
      if (!data || data.length === 0) return data || [];

      const processedData = JSON.parse(JSON.stringify(data));
      const sequenceColumn = "Â∫èÂè∑"; // ÊòéÁ°ÆÊåáÂÆöÂ∫èÂè∑ÂàóÂêçÁß∞

      // Êô∫ËÉΩÂ°´ÂÖÖÂ∫èÂè∑Âπ∂Ê∑ªÂä†ÂàÜÁªÑÊ†áËØÜ
      let lastSequenceValue = null;
      let currentGroup = null; // ÂΩìÂâçÂàÜÁªÑÊ†áËØÜ

      processedData.forEach((row, index) => {
        const currentValue = row[sequenceColumn];

        if (currentValue != null && currentValue !== "") {
          // ÈÅáÂà∞Êñ∞ÁöÑÊúâÊïàÂ∫èÂè∑ÂÄºÔºåÂºÄÂßãÊñ∞ÂàÜÁªÑ
          lastSequenceValue = currentValue;
          currentGroup = currentValue;
          row[`${sequenceColumn}_isOriginal`] = true;
        } else if (lastSequenceValue != null) {
          // Á©∫ÁôΩÂÄºÔºåÁªßÊâø‰∏ä‰∏Ä‰∏™ÊúâÊïàÂ∫èÂè∑ÁöÑÂàÜÁªÑÔºå‰ΩÜ‰∏çÂ°´ÂÖÖÂ∫èÂè∑ÂÄº
          row[`${sequenceColumn}_isFilled`] = true;
        }

        // ‰∏∫ÊØè‰∏ÄË°åÊ∑ªÂä†ÂàÜÁªÑÊ†áËØÜ
        row._sequenceGroup = currentGroup;
      });

      return processedData;
    },

    /**
     * Ë°®Ê†ºË°åÊ†∑ÂºèÁ±ªÂêç
     * @param {Object} param0 - Ë°å‰ø°ÊÅØ
     * @returns {string} Ê†∑ÂºèÁ±ªÂêç
     */
    tableRowClassName: function ({ row }) {
      // Ëé∑ÂèñÂΩìÂâçË°åÁöÑÂàÜÁªÑÊ†áËØÜ
      const sequenceGroup = row._sequenceGroup;

      // ÂàùÂßãÂåñÂàÜÁªÑÊò†Â∞Ñ
      if (!this.sequenceGroups) {
        this.sequenceGroups = new Map();
        this.currentGroupId = 0;
      }

      // ‰∏∫Êñ∞ÂàÜÁªÑÂàõÂª∫ÂàÜÁªÑIDÔºà‰ªÖÂΩìÂàÜÁªÑÊ†áËØÜÂ≠òÂú®Êó∂Ôºâ
      if (
        sequenceGroup !== undefined &&
        sequenceGroup !== null &&
        !this.sequenceGroups.has(sequenceGroup)
      ) {
        this.sequenceGroups.set(sequenceGroup, this.currentGroupId);
        this.currentGroupId++;
      }

      // Ê†πÊçÆÂàÜÁªÑIDËøîÂõû‰∫§ÊõøÊ†∑ÂºèÁ±ªÂêç
      const groupId = this.sequenceGroups.get(sequenceGroup);
      return groupId % 2 === 0 ? "group-a-row" : "group-b-row";
    },

    /**
     * ËÆ°ÁÆóË°®Ê†ºÈ´òÂ∫¶
     */
    calculateTableHeight: function () {
      // ‰ΩøÁî®$nextTickÁ°Æ‰øùDOMÂ∑≤ÂÆåÂÖ®Ê∏≤Êüì
      this.$nextTick(() => {
        if (this.isFullscreen) {
          this.tableHeight = window.innerHeight - 100;
        } else {
          // Ë∞ÉÊï¥È´òÂ∫¶ËÆ°ÁÆóÔºå‰ΩøÁî®Áà∂ÂÆπÂô®È´òÂ∫¶
          const container = this.$parent.$el.querySelector(".data-container");
          if (container) {
            // Ëé∑ÂèñÊéßÂà∂Ê†èÂÆûÈôÖÈ´òÂ∫¶
            const controls = this.$el.querySelector(".table-controls");
            const controlsHeight = controls ? controls.offsetHeight : 0;

            // ‰ªéÁà∂ÂÆπÂô®Ëé∑ÂèñÂèØÁî®È´òÂ∫¶ÔºåÂáèÂéªÊéßÂà∂Ê†èÈ´òÂ∫¶ÂíåÂÜÖËæπË∑ù
            const containerHeight = container.clientHeight;
            // Ë∞ÉÊï¥ÂÅèÁßªÈáèËÆ°ÁÆóÊñπÂºèÔºå‰ΩøÁî®ÂÆûÈôÖÊéßÂà∂Ê†èÈ´òÂ∫¶ + Â∞èÁöÑËæπË∑ù
            this.tableHeight = containerHeight - controlsHeight - 20;
          } else {
            // ÂõûÈÄÄÊñπÊ°à
            this.tableHeight = window.innerHeight - 300;
          }

          // ÊúÄÂ∞èÈ´òÂ∫¶ÈôêÂà∂
          if (this.tableHeight < 200) {
            this.tableHeight = 200;
          }
        }

        // Âº∫Âà∂Ë°®Ê†ºÈáçÊñ∞Â∏ÉÂ±Ä
        this.forceTableUpdate();
      });
    },

    /**
     * Âà§Êñ≠ÂÜÖÂÆπÊòØÂê¶ËøáÈïø
     * @param {*} value - ÂçïÂÖÉÊ†ºÂÄº
     * @returns {boolean} ÊòØÂê¶ËøáÈïø
     */
    isLongContent: function (value) {
      if (value == null || value === "") return false;
      const str = String(value);
      return str.length > 30; // Ë∂ÖËøá30‰∏™Â≠óÁ¨¶ËßÜ‰∏∫ÈïøÂÜÖÂÆπ
    },

    /**
