     * @param {number} rowIndex - Ë°åÁ¥¢Âºï
     * @param {string} column - ÂàóÂêç
     * @param {*} value - ÂçïÂÖÉÊ†ºÂÄº
     */
    handleCellHover: function (rowIndex, column, value) {
      // ÂçïÂÖÉÊ†ºÊÇ¨ÂÅúÂ§ÑÁêÜ
    },

    /**
     * Â§ÑÁêÜÁªüËÆ°Èù¢ÊùøÂçïÂÖÉÊ†ºÊÇ¨ÂÅú‰∫ã‰ª∂
     * @param {*} content - ÂçïÂÖÉÊ†ºÂÜÖÂÆπ
     * @param {string} column - ÂàóÂêç
     */
    handleStatisticCellHover: function (content, column) {
      // ‰ΩøÁî®Áé∞ÊúâÁöÑËØ¶ÊÉÖÂØπËØùÊ°ÜÊòæÁ§∫ÁªüËÆ°Èù¢ÊùøÂçïÂÖÉÊ†ºÂÜÖÂÆπ
      this.detailContent = content != null ? String(content) : "";
      this.detailColumn = column;
      this.detailRow = "ÁªüËÆ°Èù¢Êùø";
      this.detailSequenceValue = "";
      this.isFromStatistics = true; // ËÆæÁΩÆ‰∏∫‰ªéÁªüËÆ°Èù¢ÊùøÊâìÂºÄ
      this.detailDialogVisible = true;

      // ÂÖ®Â±èÊ®°ÂºèÂ±ÇÁ∫ß
      this.$nextTick(() => {
