    /**
     * Â§ÑÁêÜÁªüËÆ°ÂØπËØùÊ°ÜÊâìÂºÄ‰∫ã‰ª∂ÔºåÁ°Æ‰øùÂûÇÁõ¥Â±Ö‰∏≠
     */
    handleStatisticsDialogOpen() {
