    /**
     * ÊòæÁ§∫ÂçïÂÖÉÊ†ºËØ¶ÊÉÖÂØπËØùÊ°Ü
     * @param {*} content - ÂçïÂÖÉÊ†ºÂÜÖÂÆπ
     * @param {string} column - ÂàóÂêç
     * @param {number} row - Ë°åÂè∑
     */
    showCellDetail: function (content, column, row) {
      // ÂÖ®Â±èÊ®°Âºè‰∏ã‰∏çÊòæÁ§∫ËØ¶ÊÉÖÂØπËØùÊ°Ü
