        this.headers &&
        this.headers.length > 0
      ) {
        const firstHeader = this.headers[0];
        const rowData = this.filteredData[row - 1];
        if (rowData && firstHeader) {
          // ÊòæÁ§∫Â∫èÂè∑ÁªÑ‰ø°ÊÅØ
          this.detailSequenceValue =
            rowData._sequenceGroup || rowData[firstHeader] || "";
        } else {
          this.detailSequenceValue = "";
