    /**
     * Â§çÂà∂ËØ¶ÊÉÖÂØπËØùÊ°ÜÂÜÖÂÆπ
     */
    copyDetailContent: function () {
      this.copyToClipboard(this.detailContent);
      this.$message.success({
        message: "Â∑≤Â§çÂà∂Âà∞Ââ™Ë¥¥Êùø",
        center: true,
      });
    },

    /**
     * Â§çÂà∂ÊñáÊú¨Âà∞Ââ™Ë¥¥Êùø
     * @param {string} text - Ë¶ÅÂ§çÂà∂ÁöÑÊñáÊú¨
     */
    copyToClipboard: function (text) {
      const textarea = document.createElement("textarea");
      textarea.value = text;
      textarea.style.position = "fixed";
      textarea.style.opacity = "0";
      document.body.appendChild(textarea);
      textarea.select();

      try {
        document.execCommand("copy");
      } catch (err) {
        console.error("Â§çÂà∂Â§±Ë¥•:", err);
        this.$message.error({
          message: "Â§çÂà∂Â§±Ë¥•ÔºåËØ∑ÊâãÂä®Â§çÂà∂",
          center: true,
        });
      } finally {
        document.body.removeChild(textarea);
      }
    },

    /**
     * Ë°®Ê†ºÂçïÂÖÉÊ†ºÊ†∑ÂºèÁ±ªÂêç
     * @param {Object} param0 - ÂçïÂÖÉÊ†º‰ø°ÊÅØ
     * @returns {string} Ê†∑ÂºèÁ±ªÂêç
     */
