     */
    scrollToBottom: function () {
      const tableRef = this.$refs.dataTable;
      if (tableRef && tableRef.$el) {
        const tableWrapper = tableRef.$el.querySelector(
          ".el-table__body-wrapper"
        );
        if (tableWrapper) {
          tableWrapper.scrollTop = tableWrapper.scrollHeight;
        }
      }
    },

    /**
     * ÂàáÊç¢ÂÖ®Â±èÊ®°Âºè
     */
    toggleFullscreen: function () {
      this.isFullscreen = !this.isFullscreen;
      this.calculateTableHeight();

      // ÊéßÂà∂bodyÊªöÂä®
      if (this.isFullscreen) {
        document.body.style.overflow = "hidden";
      } else {
        document.body.style.overflow = "";
      }

      // Âº∫Âà∂Ë°®Ê†ºÈáçÊñ∞Â∏ÉÂ±Ä
      this.$nextTick(function () {
        this.forceTableUpdate();
      });
    },

    /**
     * Ëé∑ÂèñÂàóÂÆΩÂ∫¶
     * @param {string} header - ÂàóÂ§¥
     * @returns {number} ÂàóÂÆΩÂ∫¶
     */
    getColumnWidth: function (header) {
      if (!this.allData || this.allData.length === 0) return 250;

      // ÂèñÂâç100Ë°åÊï∞ÊçÆËÆ°ÁÆóÊúÄÂ§ßÂÜÖÂÆπÈïøÂ∫¶
      const sampleValues = this.allData.slice(0, 100).map(function (row) {
        const value = row[header];
        if (value === null || value === undefined) return "";
        return String(value);
      });

      // ËÆ°ÁÆóÊúÄÂ§ßÈïøÂ∫¶ÔºàËÄÉËôëÂàóÂ§¥ÂíåÂÜÖÂÆπÔºâ
      const maxLength = Math.max(
        header.length,
        ...sampleValues.map(function (str) {
          return str.length;
        })
      );

      // ËÆ°ÁÆóÊúÄÂ∞èÂÆΩÂ∫¶
      const minWidth = Math.max(250, Math.min(maxLength * 15, 800));
      return minWidth;
    },

    /**
     * ÊòæÁ§∫ÁªüËÆ°ÂØπËØùÊ°ÜÂπ∂ËÆ°ÁÆóÁªüËÆ°Êï∞ÊçÆ
     */
    showStatistics() {
      this.calculateStatistics();
      this.statisticsDialogVisible = true;
    },

    /**
     * ËÆ°ÁÆóÁªüËÆ°Êï∞ÊçÆ
     */
    calculateStatistics() {
      if (!this.filteredData || this.filteredData.length === 0) {
        this.statisticsData = [
          {
            articleCount: 0,
            countryCount: 0,
            mediaCount: 0,
            totalViews: 0,
            mediaDetails: "Êó†Êï∞ÊçÆ",
            signatureInfo: this.hasSignatureArticleTag ? "" : undefined, // ÁΩ≤Âêç‰ø°ÊÅØÂ≠óÊÆµ
          },
        ];
        return;
      }

      // ÂõΩÂÆ∂Êï∞ÔºàÂéªÈáçÔºâ
      const countries = new Set();
      // Â™í‰ΩìÊï∞ÔºàÂéªÈáçÔºâ
      const medias = new Map();
      // ÊÄªËêΩÂú∞Ê¨°Êï∞
      let totalViews = 0;
      // ÊÄªÁØáÊï∞Ôºà‰∏çÂêåÂ∫èÂè∑ÁöÑÊï∞ÈáèÔºâ
      const sequenceNumbers = new Set();
      const sequenceColumn = this.headers[0] || "Â∫èÂè∑"; // Ëé∑ÂèñÂ∫èÂè∑ÂàóÂêçÁß∞

      // Â§ÑÁêÜÁΩ≤Âêç‰ΩúËÄÖ+ÊñáÁ´†Âêç
      let signatureArticles = [];
      if (this.hasSignatureArticleTag) {
        // ÊåâÂ∫èÂè∑ÁªÑÂàÜÁªÑ
        const sequenceGroups = new Map();
        this.filteredData.forEach((row) => {
          const groupId = row._sequenceGroup;
          if (!groupId) return;

          if (!sequenceGroups.has(groupId)) {
            sequenceGroups.set(groupId, []);
          }
          sequenceGroups.get(groupId).push(row);
        });

        // Â§ÑÁêÜÊØè‰∏™ÂàÜÁªÑÁöÑÁ¨¨‰∏Ä‰∏™Ê†áÈ¢ò
        sequenceGroups.forEach((groupRows) => {
          const firstRow = groupRows[0];
          if (
            !firstRow["Á®ø‰ª∂Ê†áÈ¢ò"] ||
            !firstRow["Á®ø‰ª∂Ê†áÈ¢ò"].includes("ÁΩ≤ÂêçÊñáÁ´†")
          )
            return;

          const title = firstRow["Á®ø‰ª∂Ê†áÈ¢ò"];
          // ÊâæÂà∞"ÁΩ≤ÂêçÊñáÁ´†"ÂêéÁöÑÁ¨¨‰∏Ä‰∏™"„Äã"
          const ÁΩ≤ÂêçÊñáÁ´†Index = title.indexOf("ÁΩ≤ÂêçÊñáÁ´†");
          if (ÁΩ≤ÂêçÊñáÁ´†Index === -1) return;

          // ‰ªé"ÁΩ≤ÂêçÊñáÁ´†"‰πãÂêéÂºÄÂßãÊü•ÊâæÁ¨¨‰∏Ä‰∏™"„Äã"
          const startSearchIndex = ÁΩ≤ÂêçÊñáÁ´†Index + "ÁΩ≤ÂêçÊñáÁ´†".length;
          const closingBracketIndex = title.indexOf("„Äã", startSearchIndex);

          if (closingBracketIndex !== -1) {
            // Êà™Âèñ‰ªéÂºÄÂ§¥Âà∞ÊâæÂà∞ÁöÑ"„Äã"ÔºàÂåÖÂê´"„Äã"Ôºâ
            const processedTitle = title.substring(0, closingBracketIndex + 1);
            signatureArticles.push(processedTitle);
          }
        });
      }

      this.filteredData.forEach((row) => {
        // ÁªüËÆ°ÂõΩÂÆ∂
        if (row["ÂõΩÂÆ∂"] && row["ÂõΩÂÆ∂"] !== "") {
          countries.add(row["ÂõΩÂÆ∂"]);
        }

        // ÁªüËÆ°Â™í‰Ωì - Ê†áÂáÜÂåñÂ§ÑÁêÜÂêéÂéªÈáç
        if (row["ËêΩÂú∞Â™í‰Ωì"] && row["ËêΩÂú∞Â™í‰Ωì"] !== "") {
          // Ê†áÂáÜÂåñÂ™í‰ΩìÂêçÁß∞ÔºöÂéªÈô§ÂâçÂêéÁ©∫Ê†ºÔºåËΩ¨Êç¢‰∏∫Â∞èÂÜôÁî®‰∫éÂéªÈáçÂà§Êñ≠
          const normalizedMedia = row["ËêΩÂú∞Â™í‰Ωì"].trim().toLowerCase();
          // Â¶ÇÊûú‰∏çÂ≠òÂú®ÔºåÊ∑ªÂä†Âà∞Map‰∏≠Ôºå‰øùÁïôÂéüÂßãÂêçÁß∞
          if (!medias.has(normalizedMedia)) {
            medias.set(normalizedMedia, row["ËêΩÂú∞Â™í‰Ωì"].trim());
          }
        }

        // ÁªüËÆ°ËêΩÂú∞Ê¨°Êï∞
        if (row["ËêΩÂú∞Ê¨°Êï∞"] && !isNaN(row["ËêΩÂú∞Ê¨°Êï∞"])) {
          totalViews += Number(row["ËêΩÂú∞Ê¨°Êï∞"]);
        }
        // ÁªüËÆ°ÊÄªÁØáÊï∞Ôºà‰∏çÂêåÂ∫èÂè∑ÁöÑÊï∞ÈáèÔºâ
        if (row[sequenceColumn] && row[sequenceColumn] !== "") {
          sequenceNumbers.add(row[sequenceColumn]);
        }
      });

      // Â™í‰ΩìËØ¶ÊÉÖÔºàÂéªÈáçÂêéÁî®È°øÂè∑ÂàÜÈöîÔºâ
      const mediaDetails = Array.from(medias.values()).join("„ÄÅ");

      this.statisticsData = [
        {
          articleCount: sequenceNumbers.size,
          countryCount: countries.size,
          mediaCount: medias.size,
          totalViews: totalViews,
          mediaDetails: mediaDetails || "Êó†Êï∞ÊçÆ",
          signatureInfo: this.hasSignatureArticleTag
            ? signatureArticles.join("\n")
            : undefined, // ÁΩ≤Âêç‰ø°ÊÅØÂ≠óÊÆµ
        },
      ];
    },

    /**
     * Â§çÂà∂ÁªüËÆ°ÁªìÊûú
     */
    copyStatistics() {
      if (!this.statisticsData || this.statisticsData.length === 0) return;

      const stats = this.statisticsData[0];
      const text = `ÂõΩÂÆ∂Êï∞: ${stats.countryCount}\nÂ™í‰ΩìÊï∞: ${stats.mediaCount}\nÊÄªËêΩÂú∞Ê¨°Êï∞: ${stats.totalViews}\nÂ™í‰ΩìËØ¶ÊÉÖ: ${stats.mediaDetails}`;

      this.copyToClipboard(text);
      this.$message.success({
        message: "ÁªüËÆ°ÁªìÊûúÂ∑≤Â§çÂà∂Âà∞Ââ™Ë¥¥Êùø",
        center: true,
      });
    },

    /**
     * Ê†ºÂºèÂåñÂçïÂÖÉÊ†ºÂÄº
     * @param {*} value - ÂéüÂßãÂÄº
     * @param {Object} row - ÂΩìÂâçË°åÊï∞ÊçÆ
     * @param {string} column - ÂΩìÂâçÂàóÂêç
     * @returns {string} Ê†ºÂºèÂåñÂêéÁöÑÂÄº
     */
    formatCellValue: function (value, row, column) {
      if (value == null) return "";

      // Ëé∑ÂèñÂΩìÂâçÂàóÊòØÂê¶‰∏∫Â∫èÂè∑Âàó
      const firstHeader = this.headers[0] || "Â∫èÂè∑";

      // Â∫èÂè∑ÂàóÊ†ºÂºèÂåñ - ‰ªÖÊòæÁ§∫ÂéüÂßãÂ∫èÂè∑ÔºåÂàÜÁªÑË°å‰∏çÊòæÁ§∫
      if (column === firstHeader) {
        return row[`${firstHeader}_isOriginal`] ||
          row[`${firstHeader}_isFiltered`]
          ? value
          : "";
      }

      // Êï∞Â≠óÊ†ºÂºèÂåñ
      if (typeof value === "number") {
        if (Number.isInteger(value)) {
          return value.toLocaleString(); // Êï¥Êï∞Ê†ºÂºèÂåñÔºàÂ∏¶ÂçÉÂàÜ‰ΩçÔºâ
        }
        return Number(value.toFixed(2)).toLocaleString(); // Â∞èÊï∞Ê†ºÂºèÂåñÔºà‰øùÁïô‰∏§‰ΩçÂ∞èÊï∞Ôºâ
      }

      // Â∏ÉÂ∞îÂÄºÊ†ºÂºèÂåñ
      if (typeof value === "boolean") {
        return value ? "ÊòØ" : "Âê¶";
      }

      // ÂÖ∂‰ªñÁ±ªÂûãËΩ¨‰∏∫Â≠óÁ¨¶‰∏≤
      return String(value);
    },

    /**
     * ÂØπËØùÊ°ÜÂ±ÇÁ∫ß
     */
    fixDialogZIndex: function () {
      if (!this.detailDialogVisible) return;

      this.$nextTick(() => {
        // ÈÄâÊã©ËØ¶ÊÉÖÂØπËØùÊ°ÜÁöÑÂåÖË£ÖÂô®ÂíåÈÅÆÁΩ©Â±Ç
        const detailDialogWrapper = document.querySelector(
          ".cell-detail-dialog .el-dialog__wrapper"
        );
        const detailModal = document.querySelector(
          ".cell-detail-dialog + .v-modal"
        );

        if (detailDialogWrapper && detailModal) {
          // ËÆæÁΩÆÊûÅÈ´òÁöÑz-indexÁ°Æ‰øùÂú®ÊâÄÊúâÂÖÉÁ¥†‰πã‰∏ä
          // Â§ßÂπÖÊèêÈ´òÂÖ®Â±èÊ®°Âºè‰∏ãÁöÑz-indexÂÄºÔºåÁ°Æ‰øùË∂ÖËøáË°®Ê†ºÂÆπÂô®ÁöÑ9999
          const baseZIndex = this.isFullscreen ? 100000 : 2000;
          detailDialogWrapper.style.zIndex = baseZIndex + 2;
          detailModal.style.zIndex = baseZIndex + 1;

          // Á°Æ‰øùÂØπËØùÊ°ÜÊú¨Ë∫´‰πüÁªßÊâøÈ´òz-index
          const dialogElement = detailDialogWrapper.querySelector(".el-dialog");
          if (dialogElement) {
            dialogElement.style.zIndex = baseZIndex + 2;
          }
        }
      });
    },
    /**
     * ÊòæÁ§∫ÂàÜÂ™í‰ΩìÁªüËÆ°ÂØπËØùÊ°Ü
     */
    showMediaStatistics() {
      this.calculateMediaStatistics();
      this.mediaStatisticsDialogVisible = true;
    },

    /**
     * ËÆ°ÁÆóÂàÜÂ™í‰ΩìÁªüËÆ°Êï∞ÊçÆ
     */
    calculateMediaStatistics() {
      if (!this.filteredData || this.filteredData.length === 0) {
        this.mediaStatisticsData = [];
        return;
      }

      // Â™í‰ΩìÁªüËÆ°Map: key‰∏∫Â™í‰ΩìÂêçÁß∞, value‰∏∫{views: Ê¨°Êï∞, articles: ÁØáÊï∞, sequences: Â∫èÂè∑ÈõÜÂêà}
      const mediaStats = new Map();
      const sequenceColumn = this.headers[0] || "Â∫èÂè∑"; // Ëé∑ÂèñÂ∫èÂè∑ÂàóÂêçÁß∞

      this.filteredData.forEach((row) => {
        if (!row["ËêΩÂú∞Â™í‰Ωì"] || row["ËêΩÂú∞Â™í‰Ωì"] === "") return;

        // Ê†áÂáÜÂåñÂ™í‰ΩìÂêçÁß∞
        const normalizedMedia = row["ËêΩÂú∞Â™í‰Ωì"].trim().toLowerCase();
        const originalMedia = row["ËêΩÂú∞Â™í‰Ωì"].trim();

        // Ëé∑ÂèñÂ∫èÂè∑ÁªÑÊ†áËØÜÔºàÂéü‰∏∫Ëé∑ÂèñÂ∫èÂè∑ÂàóÂÄºÔºâ
        const sequenceGroup = row._sequenceGroup || "";

        // Ëé∑ÂèñËêΩÂú∞Ê¨°Êï∞
        const views = row["ËêΩÂú∞Ê¨°Êï∞"] ? Number(row["ËêΩÂú∞Ê¨°Êï∞"]) : 0;

        // Â¶ÇÊûúÂ™í‰Ωì‰∏çÂ≠òÂú®, ÂàùÂßãÂåñ
        if (!mediaStats.has(normalizedMedia)) {
          mediaStats.set(normalizedMedia, {
            media: originalMedia,
            views: 0,
            articles: 0,
            sequences: new Set(),
          });
        }

        const stats = mediaStats.get(normalizedMedia);
        stats.views += views;

        // Â¶ÇÊûúÊúâÊúâÊïàÁöÑÂ∫èÂè∑ÁªÑ, Ê∑ªÂä†Âà∞ÈõÜÂêà‰∏≠ÔºàÂéü‰∏∫Âà§Êñ≠Â∫èÂè∑ÂàóÂÄºÔºâ
        if (sequenceGroup) {
          stats.sequences.add(sequenceGroup);
          stats.articles = stats.sequences.size; // ÁØáÊï∞Á≠â‰∫é‰∏çÂêåÂ∫èÂè∑ÁªÑÁöÑÊï∞Èáè
        }
      });

      // ËΩ¨Êç¢‰∏∫Êï∞ÁªÑÂπ∂ÊéíÂ∫è
      this.mediaStatisticsData = Array.from(mediaStats.values()).sort(
        (a, b) => b.views - a.views
      ); // ÊåâËêΩÂú∞Ê¨°Êï∞ÈôçÂ∫èÊéíÂàó

      // Ê∑ªÂä†ÊÄªËÆ°Ë°å
      const totalViews = this.mediaStatisticsData.reduce(
        (sum, item) => sum + item.views,
        0
      );
      const totalArticles = this.mediaStatisticsData.reduce(
        (sum, item) => sum + item.articles,
        0
      );
      this.mediaStatisticsData.push({
        media: "ÊÄªËÆ°",
        views: totalViews,
        articles: totalArticles,
        isTotal: true,
      });
    },

    /**
     * Â§çÂà∂ÂàÜÂ™í‰ΩìÁªüËÆ°ÁªìÊûú
     */
    copyMediaStatistics() {
      if (!this.mediaStatisticsData || this.mediaStatisticsData.length === 0)
        return;

      let text = "Â™í‰Ωì\tÊ¨°Êï∞\tÁØáÊï∞\n";
      this.mediaStatisticsData.forEach((item) => {
        text += `${item.media}\t${item.views}\t${item.articles}\n`;
      });

      this.copyToClipboard(text);
      this.$message.success({
        message: "ÂàÜÂ™í‰ΩìÁªüËÆ°ÁªìÊûúÂ∑≤Â§çÂà∂Âà∞Ââ™Ë¥¥Êùø",
        center: true,
      });
    },
  },
};
</script>

